<script>

import FoodMenu from '@/views/FoodMenu'

let postsRef = firebase.database().ref('posts');

export default {
  name: 'timeline',
  components: { FoodMenu },
  data() {
    return {
      allPosts: [],
      allFields: []
    }
  },
  created: function() {
    postsRef.on('child_added', snapshot => {
      this.allPosts.push(snapshot.val());
      this.allFields.push(Object.keys(this.allPosts[0].values[0]));
    });
    

  },
  methods: {

  }
}
</script>

